.NewTodoForm{
    margin-top: 3rem;
    display: inline-block;
}
.NewTodoForm label {
    min-width: 100%;
    margin-bottom: .5rem;
    font-size: 1.3rem;
}
.NewTodoForm input {
    width: 22em;
    border: none;
    background: #f7f1f1;
    padding: 0 1.25em;
    font-size: initial;
    outline: none;
}
.NewTodoForm button {
    padding: 0 1.3rem;
    background: rgba(255, 255, 255, 0.2);
    color: #fff;
    text-transform: uppercase;
    font-weight: bold;
    border: 1px solid rgba(255,255,255,0.3);
    margin-left: 1em;
    cursor: pointer;
    transition: background 0.2s ease-out;
    outline: none;
}
.NewTodoForm button:hover {
    background: rgba(0, 0, 0, 0.1);
  }
.NewTodoForm input, .NewTodoForm button{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    height: 3rem;
}
